module.exports = {
  getCookie(key){
    let cookie = document.cookie
    if(!key){
      return cookie
    }
    let reg = new RegExp('[\\s^]' + key + '=([^;]*)')
    let res = cookie.match(reg)
    if(res){
      return res[1]
    }
  },
  setCookie(key, value, day){
    let date = new Date()
    date.setTime(date.getTime() + day * 24 * 60 * 60 * 1000)
    document.cookie = `${key}=${value};expires=${date.toGMTString()}`
  },
  delCookie(key){
    document.cookie = `${key}=;expires=${new Date(1).toGMTString()}`
  }
}



