<template>
  <div>
    <h1 class="center">ADVANCE.AI</h1>
    <div class="login-wrapper">
      <div class="login-info login-user">
        <input type="tel" v-model="username" @focus="hideErr" placeholder="请输入手机号码">
      </div>
      <div class="login-info login-pwd">
        <input type="password" v-model="password" @focus="hideErr" placeholder="请输入密码">
      </div>
      <div class="center">
        <button class="btn" @click="doLogin">登录</button>
      </div>
      <div v-show="isShow" class="center err-wrapper"><span class="red">{{errMsg}}</span></div>
    </div>
  </div>
</template>
<script>
  export default{
    name   : 'Login',
    data(){
      return {
        username: '',
        password: '',
        isShow  : false,
        errMsg  : ''
      }
    },
    watch: {
      username(newVal){
        this.username = newVal.length > 11 ? newVal.substring(0, 11) : newVal
      }
    },
    methods: {
      showErr(msg){
        this.isShow = true
        this.errMsg = msg
      },
      hideErr(){
        this.isShow = false
        this.errMsg = ''
      },
      doLogin(){
        let validate = {
          isNotEmpty: (value, msg) => {
            if (!value) {
              this.showErr(msg)
            }else{
              return true
            }
          },
          isMatch: (value, reg, msg) => {
            if (!value.match(reg)) {
              this.showErr(msg)
            }else{
              return true
            }
          }
        }
        if(validate.isNotEmpty(this.username, '请输入手机号码')
          && validate.isNotEmpty(this.password, '请输入密码')
          && validate.isMatch(this.username, /^1[35678]\d{9}$/, '请输入正确的手机号码')
        ){
          this.axios({
            method: 'post',
            url   : 'http://test.advance.ai/login',
            data  : {
              username: this.username,
              password: this.password
            }
          }).then(res => {
            if (res.data.resCode === 2) {
              // 成功，进入welcome
              this.$router.push({path: '/Welcome'})
            } else {
              // 失败，显示失败信息
              this.showErr(res.data.msg)
            }
          })
        }
        
      }
    }
  }
</script>
<style type="text/less" lang="less" scoped>
  @color: #ccc;
  .login-wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .login-info {
      margin: 10px;
      input {
        width: 200px;
        height: 20px;
        padding: 5px;
        padding-left: 25px;
        border: 1px solid @color;
        outline: none;
        background-repeat: no-repeat;
        background-size: auto 60%;
        background-position: 4px center;
        transition: all .2s;
        &:focus {
          border: 1px solid #aaa;
        }
      }
    }
    .login-user {
      input {
        background-image: url(../assets/tel.png);
      }
    }
    .login-pwd {
      input {
        background-image: url(../assets/pwd.png);
      }
    }
    .btn {
      width: 100px;
      padding: 8px 0;
      border: 1px solid #ccc;
      border-radius: 2px;
      outline: none;
      background: none;
      transition: all .2s;
      cursor: pointer;
      &:hover {
        background-color: #eee;
      }
    }
    .err-wrapper {
      position: absolute;
      width: 100%;
      line-height: 30px;
      bottom: -30px;
    }
  }
  
  .center {
    text-align: center;
  }
  
  .red {
    color: #f00;
  }
</style>



