import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Welcome from '@/components/Welcome'
import { getCookie } from '../cookie'

Vue.use(Router)
let router = new Router({
  routes: [
    {
      path     : '/Login',
      name     : 'Login',
      component: Login
    }, {
      path     : '/Welcome',
      name     : 'Welcome',
      component: Welcome
    }, {
      path    : '*',
      redirect: '/Login'
    }
  ]
})
router.beforeEach((to, from, next) => {
  let hasLogin = getCookie('session_id')
  // 如果登录则不展示登录页
  if(hasLogin){
    if(to.name === 'Login'){
      router.push('Welcome')
    }
  }else{
    // 如果未登录，跳转至登录页
    if (to.name !== 'Login') {
      router.push('Login')
    }
  }
  next()
})
export default router



