import Mock from 'mockjs'
import { setCookie } from './cookie'

//emmm...为什么手机号是要username呢，奇怪的需求
let database = [
  {username: '18435155822', password: '256BA7535D9CA10895DB6F7E3D224CD2'},
  {username: '17610965822', password: '7E9A189720D3E12990C04E560A68B8B2'},
]
let sessionSet = {}
Mock.mock('http://test.advance.ai/login', 'post', function (options) {
  let {username, password} = JSON.parse(options.body)
  password = md5(password)
  let user
  for(let i=0; i<database.length; i++){
    if (database[i].username === username) {
      user = database[i]
    }
  }
  if(user){
    if (password === user.password) {
      let session_id = random()
      sessionSet[session_id] = username
      setCookie('session_id', session_id, 30) // 就当有session_id吧
      setCookie('username', username, 30) // 就当有session_id吧
      // 登陆成功
      return {
        resCode: 2,
        msg    : '成功'
      }
    } else {
      return {
        resCode: 1,
        msg    : '用户密码错误'
      }
    }
  } else {
    return {
      resCode: 4,
      msg    : '用户不存在'
    }
  }
  
})
function md5(param){
  // 就当是md5吧
  if(param === '123456'){
    return '256BA7535D9CA10895DB6F7E3D224CD2'
  }else if(param === '654321'){
    return '7E9A189720D3E12990C04E560A68B8B2'
  }
}
function random(){
  return Math.random().toString().substring(2)
}



