<template>
  <div>
    <h1 class="center">Hello,{{username}}</h1>
    <div class="center logout"><span @click="logout">注销</span></div>
  </div>
</template>
<script>
  import { getCookie, delCookie } from '../cookie'
  export default{
    name: 'Welcome',
    data(){
      return {
        username: ''
      }
    },
    methods: {
      logout(){
        // 发logout请求
        delCookie('session_id')
        // 刷新页面自动跳回登录页
        this.$router.go(0)
      }
    },
    created(){
      this.username = getCookie('username')
    }
  }
</script>
<style scoped type="text/less" lang="less">
  .center {
    text-align: center;
  }
  .logout {
    color: #666;
    cursor: pointer;
    &:hover{
      color: #333;
    }
  }
</style>



